
import { PropType } from 'vue';
import { normalSearch } from '../../../utils/Search';
import { searchResult } from '../../definitions';

let searchTimeout;
export default {
  components: {},
  props: {
    type: {
      type: String as PropType<'anime' | 'manga'>,
      default: 'anime',
    },
    keyword: {
      type: String,
      default: '',
    },
    syncMode: {
      type: Boolean,
      default: false,
    },
    currentId: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      items: [] as searchResult[],
      loading: false,
      searchKeyword: '',
    };
  },
  watch: {
    keyword() {
      this.searchKeyword = this.keyword;
      this.load();
    },
    searchKeyword() {
      clearTimeout(searchTimeout);
      searchTimeout = setTimeout(() => {
        this.load();
      }, 200);
    },
    type() {
      this.load();
    },
  },
  mounted() {
    if (this.syncMode) {
      this.searchKeyword = this.keyword;
      this.load();
    }
  },
  methods: {
    lang: api.storage.lang,
    getStatusText: utils.getStatusText,
    episodeText: utils.episode,
    load() {
      if (this.searchKeyword) {
        this.loading = true;

        normalSearch(this.searchKeyword, this.type).then(items => {
          this.loading = false;
          this.items = items;
          this.$nextTick(() => {
            this.$el.scrollIntoView({ behavior: 'smooth' });
          });
        });
      }
    },
    inputFocus() {
      if (!this.searchKeyword) {
        this.searchKeyword = this.keyword;
      }
    },
    async clickItem(e, item) {
      e.preventDefault();
      if (!item) {
        this.$emit('clicked', { url: '', id: 0 });
        return;
      }
      const url = await item.malUrl();
      if (url) {
        this.$emit('clicked', { url, id: item.id });
      } else {
        this.$emit('clicked', { url: item.url, id: item.id });
      }
    },
  },
};
