
export default {
  props: {
    obj: {
      type: Object,
      default: undefined,
    },
  },
  data() {
    return {};
  },
  computed: {
    rules() {
      if (this.obj && this.obj.getRules()) {
        return this.obj.getRules().sort((a, b) => a.from.start - b.from.start);
      }
      return [];
    },
  },
  methods: {
    lang: api.storage.lang,
    activeRule(rule) {
      return {
        active: this.obj ? rule === this.obj.activeRule : false,
      };
    },
  },
};
